<style scoped>
.page {
    background-color: #fff;
}

.inner-page {
    padding: 16px 15px;
}

.title {
    font-size: 14px;
    color: #000;
    height: 38px;
    line-height: 38px;
    background-color: #f8f8f8;
    margin-top: 12px;
    padding-left: 14px;
}

.project_name,
.project_desc,
.label-lists {
    margin-top: 14px;
}

.label-lists {
    border-bottom: none !important;
}

.label-lists>div {
    padding: 4px;
    padding-bottom: 4px;
}

.label-lists a {
    display: inline-block;
    color: #000;
    font-size: 12px;
    padding: 6px 8px;
    border-radius: 15px;
    border: 1px solid #ccc;
    margin-right: 5px;
    margin-bottom: 5px;
    text-decoration: none;
}

.labels_null {
    text-align: center;
    font-size: 12px;
    color: #ccc;
    padding: 20px;
}

.mint-checklist {
    border-bottom: 1px solid #ccc;
}

.mint-checklist-title {
    font-size: 14px;
    margin-left: 0;
}

.mint-checklist input,
.mint-checklist textarea {
    font-size: 12px;
}

.mint-button--primary {
    margin-top: 15px;
}

.wrapper-project-label {
    display: flex;
    display: -webkit-flex;
    /* Safari */
    flex-direction: row;
    justify-content: space-between;
    color: #000;
    font-size: 14px;
}

.wrapper-project-label a {
    text-decoration: none;
    font-size: 12px;
    color: #3C96FF;
}

.project-label-edit {
    color: #63acf7;
}

input,
textarea {
    user-select: auto;
}

</style>
<template>
    <div class="page">
        <div class="inner-page">
            <!-- project-bg -->
            <project-bg :projectInfo="projectInfo"></project-bg>
            <!-- /project-bg -->
            <div class="title">项目资料</div>
            <div class="mint-checklist project_name">
                <label class="mint-checklist-title">项目名称</label>
                <input type="text" v-model.trim="projectInfo.name" placeholder="请输入项目名称" class="mint-field-core">
            </div>
            <div class="mint-checklist project_desc">
                <label class="mint-checklist-title">项目简介</label>
                <textarea v-model.trim="projectInfo.profile" placeholder="请输入项目简介" class="mint-field-core"></textarea>
            </div>
            <div class="mint-checklist label-lists">
                <ul class="wrapper-project-label">
                    <li class="project-label">项目标签</li>
                    <li class="project-label-edit" @click="editProjectLabels()">编辑</li>
                </ul>
                <!-- label -->
                <div v-if="(projectInfo.labelList && projectInfo.labelList.length)">
                    <a href="javascript:;" v-for="(label,index) in projectInfo.labelList" @click="deleteLabel(label,index,projectInfo.labelList)" :key="label.id">{{label.name}}</a>
                </div>
                <!-- /label -->
                <!-- no label -->
                <div v-if="(projectInfo.labelList && !projectInfo.labelList.length)" class="labels_null">
                    没有相关标签,请编辑标签
                </div>
                <!-- /no label -->
            </div>
            <div class="mint-checklist">
                <label class="mint-checklist-title">项目详细描述</label>
                <textarea v-model.trim="projectInfo.detail" placeholder="请输入项目详细描述" class="mint-field-core"></textarea>
            </div>
            <mt-button size="large" class="mint-button--primary" @click.native="editProject()">确定</mt-button>
            <!-- reset -->
            <mt-button size="large" class="mint-button mint-button--primary mint-button--large is-plain" type="primary" @click.native="reset()">取消</mt-button>
            <!-- /reset -->
        </div>
    </div>
</template>
<script>
import { Toast, MessageBox, Indicator } from 'mint-ui';
import { contains } from '../util';
export default {
    name: 'item_edit',
    data() {
        return {
            projectInfo: {},
        }
    },
    components: {
        projectBg(resolve) {
            require(['./_item_edit_bg.vue'], resolve);
        },
    },
    //project-bg
    created() {
        this.getProjectInfo();
    },
    methods: {
        getProjectInfo() {
            this.$http.post("/project/getProjectInfo.do", {
                id: this.$route.params.id
            }, {
                emulateJSON: true
            }).then(function(res) {
                if (res.data.success) {
                    let data = res.data;
                    this.projectInfo = data.project;

                } else {
                    Toast(res.data.msg)
                }
            });
        },

        //修改项目
        editProject() {
            if (!this.projectInfo.name) {
                Toast('项目名称不能为空！');
                return false;
            }
            if (!this.projectInfo.profile) {
                Toast('项目描述不能为空！');
                return false;
            }

            this.$http.post("/project/modifyProjectInfo.do", {
                id: this.$route.params.id,
                name: this.projectInfo.name, //项目名称
                profile: this.projectInfo.profile, //项目描述
                detail: this.projectInfo.detail, //项目详情
            }, {
                emulateJSON: true
            }).then(function(res) {
                if (res.data.success) {
                    Toast('修改成功')

                    setTimeout(() => {
                        location.href = "/item_detail/" + this.$route.params.id;
                    }, 500);

                } else {
                    Toast(res.data.msg)
                }
            });
        },

        //reset

        reset() {
            this.getProjectInfo();
        },

        //editProjectLabels
        editProjectLabels() {
            MessageBox.prompt('添加项目标签').then(({ value, action }) => {

                if (!value) {
                    return false;
                }

                let labelList = this.projectInfo && this.projectInfo.labelList;
                let inArray = contains(labelList, { name: value });

                if (inArray) {
                    Toast('标签名称不能重复！')
                    return false;
                }

                //createProjectLabel
                this.$http.post("/label/createProjectLabel.do", {
                    name: value,
                    projectId: this.$route.params.id,
                }, {
                    emulateJSON: true
                }).then(function(res) {
                    if (res.data.success) {
                        Toast('创建项目标签成功')
                        this.projectInfo.labelList.push({
                            name: value,
                            id: res && res.data.labelId
                        })

                    } else {
                        Toast(res.data.msg)
                    }
                });
                //
            });
        },

        //deleteLabel

        deleteLabel(data, index, datas) {
            MessageBox.confirm('确定删除此标签?').then(action => {
                if (action === 'confirm') {
                    //
                    this.$http.post("/label/deleteLabel.do", {
                        id: data.id,
                    }, {
                        emulateJSON: true
                    }).then(function(res) {
                        if (res.data.success) {
                            Toast('删除标签成功')
                            datas.splice(index, 1)
                        } else {
                            Toast(res.data.msg)
                        }
                    });
                    //
                }
            });
        },
    },
};

</script>



